<template>
  <div class="rules-container">
    <div class="page-header">
      <h1></h1>
      <div>
        <el-button type="primary" @click="$router.push('/servers')"></el-button>
        <el-button type="success" @click="deployIptatoScript" :loading="deploying" :disabled="!isServerOnline"></el-button>
        <el-button type="danger" @click="confirmClearRules" :disabled="!isServerOnline"></el-button>
      </div>
    </div>

    <div v-if="server" class="server-info">
      <h2>{{ server.name }} <el-tag :type="server.status === 'online' ? 'success' : 'danger'">{{ server.status === 'online' ? '' : '' }}</el-tag></h2>
      <p>{{ server.host }}:{{ server.port }} ({{ server.username }})</p>
    </div>

    <el-tabs v-model="activeTab" type="card">
      <el-tab-pane label="" name="outbound">
        <el-alert
          v-if="!isServerOnline"
          title=""
          type="warning"
          description=""
          show-icon
          :closable="false"
          style="margin-bottom: 15px;">
        </el-alert>
        <div v-loading="!isServerOnline">
          <el-card>
            <div slot="header">
              <span></span>
              <el-button style="float: right; padding: 3px 0" type="text" @click="refreshBlockList" :disabled="!isServerOnline"></el-button>
            </div>
            
            <pre v-if="blockList" class="output">{{ blockList }}</pre>
            <div v-else>...</div>
          </el-card>

          <el-card style="margin-top: 20px;">
            <div slot="header">
              <span></span>
            </div>
            <el-button-group>
              <el-button type="primary" @click="blockBTPT" :loading="loading" :disabled="!isServerOnline">BT/PT</el-button>
              <el-button type="primary" @click="blockSPAM" :loading="loading" :disabled="!isServerOnline">SPAM</el-button>
              <el-button type="primary" @click="blockAll" :loading="loading" :disabled="!isServerOnline"></el-button>
            </el-button-group>
            
            <el-divider></el-divider>
            
            <el-form :inline="true" @submit.native.prevent="blockCustomPorts">
              <el-form-item label="">
                <el-input v-model="customPorts" placeholder=": 6881,6882-6889" :disabled="!isServerOnline"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="warning" @click="blockCustomPorts" :loading="loading" :disabled="!isServerOnline"></el-button>
              </el-form-item>
            </el-form>
            
            <el-form :inline="true" @submit.native.prevent="blockCustomKeyword">
              <el-form-item label="">
                <el-input v-model="customKeyword" placeholder=": youtube.com" :disabled="!isServerOnline"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="warning" @click="blockCustomKeyword" :loading="loading" :disabled="!isServerOnline"></el-button>
              </el-form-item>
            </el-form>
          </el-card>

          <el-card style="margin-top: 20px;">
            <div slot="header">
              <span></span>
            </div>
            <el-button-group>
              <el-button type="success" @click="unblockBTPT" :loading="loading" :disabled="!isServerOnline">BT/PT</el-button>
              <el-button type="success" @click="unblockSPAM" :loading="loading" :disabled="!isServerOnline">SPAM</el-button>
              <el-button type="success" @click="unblockAll" :loading="loading" :disabled="!isServerOnline"></el-button>
            </el-button-group>
            
            <el-divider></el-divider>
            
            <el-form :inline="true" @submit.native.prevent="unblockCustomPorts">
              <el-form-item label="">
                <el-input v-model="customUnblockPorts" placeholder=": 6881,6882-6889" :disabled="!isServerOnline"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="success" @click="unblockCustomPorts" :loading="loading" :disabled="!isServerOnline"></el-button>
              </el-form-item>
            </el-form>
            
            <el-form :inline="true" @submit.native.prevent="unblockCustomKeyword">
              <el-form-item label="">
                <el-input v-model="customUnblockKeyword" placeholder=": youtube.com" :disabled="!isServerOnline"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="success" @click="unblockCustomKeyword" :loading="loading" :disabled="!isServerOnline"></el-button>
              </el-form-item>
            </el-form>
            
            <el-button type="success" @click="unblockAllKeywords" :loading="loading" :disabled="!isServerOnline"></el-button>
          </el-card>
        </div>
      </el-tab-pane>

      <el-tab-pane label="" name="inbound">
        <el-alert
          v-if="!isServerOnline"
          title=""
          type="warning"
          description=""
          show-icon
          :closable="false"
          style="margin-bottom: 15px;">
        </el-alert>
        <div v-loading="!isServerOnline">
          <el-card>
            <div slot="header">
              <span>SSH</span>
              <el-button style="float: right; padding: 3px 0" type="text" @click="refreshSSHPort" :disabled="!isServerOnline"></el-button>
            </div>
            
            <pre v-if="sshPortStatus" class="output">{{ sshPortStatus }}</pre>
            <div v-else>...</div>
          </el-card>

          <el-card style="margin-top: 20px;">
            <div slot="header">
              <span></span>
              <el-button style="float: right; padding: 3px 0" type="text" @click="refreshInboundPorts" :disabled="!isServerOnline"></el-button>
            </div>
            
            <el-table v-loading="loadingPorts" :data="inboundPorts" style="width: 100%">
              <el-table-column prop="port" label="" width="180"></el-table-column>
              <el-table-column prop="protocol" label="" width="100"></el-table-column>
              <el-table-column label="">
                <template slot-scope="scope">
                  <el-tooltip v-if="isSshPort(scope.row.port)" content="SSH" placement="top">
                    <el-button type="danger" size="mini" disabled></el-button>
                  </el-tooltip>
                  <el-button v-else type="danger" size="mini" @click="disallowPort(scope.row.port)" :disabled="!isServerOnline"></el-button>
                </template>
              </el-table-column>
            </el-table>
            
            <el-divider></el-divider>
            
            <el-form :inline="true" @submit.native.prevent="allowPort">
              <el-form-item label="">
                <el-input v-model="portToAllow" placeholder=": 80,443" :disabled="!isServerOnline"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="allowPort" :loading="loading" :disabled="!isServerOnline"></el-button>
              </el-form-item>
            </el-form>
          </el-card>

          <el-card style="margin-top: 20px;">
            <div slot="header">
              <span>IP</span>
              <el-button style="float: right; padding: 3px 0" type="text" @click="refreshInboundIPs" :disabled="!isServerOnline"></el-button>
            </div>
            
            <el-table v-loading="loadingIPs" :data="inboundIPs" style="width: 100%">
              <el-table-column prop="ip" label="IP" width="180"></el-table-column>
              <el-table-column label="">
                <template slot-scope="scope">
                  <el-button type="danger" size="mini" @click="disallowIP(scope.row.ip || scope.row)" :disabled="!isServerOnline"></el-button>
                </template>
              </el-table-column>
            </el-table>
            
            <el-divider></el-divider>
            
            <el-form :inline="true" @submit.native.prevent="allowIP">
              <el-form-item label="IP">
                <el-input v-model="ipToAllow" placeholder=": 192.168.1.1" :disabled="!isServerOnline"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="allowIP" :loading="loading" :disabled="!isServerOnline"></el-button>
              </el-form-item>
            </el-form>
          </el-card>
        </div>
      </el-tab-pane>
    </el-tabs>

    <el-card v-if="commandOutput" style="margin-top: 20px">
      <div slot="header">
        <span></span>
        <el-button style="float: right; padding: 3px 0" type="text" @click="clearCommandOutput"></el-button>
      </div>
      <pre :class="['output', {'output-error': commandOutput.includes('') || commandOutput.includes('')}]">{{ commandOutput }}</pre>
      
      <div v-if="commandOutput.includes('status code 500')" class="error-solution">
        <el-alert
          title="(500)"
          type="error"
          description=""
          show-icon
          :closable="false">
        </el-alert>
        <el-collapse style="margin-top: 10px;">
          <el-collapse-item title="" name="1">
            <ol>
              <li>SSH</li>
              <li></li>
              <li></li>
              <li> (/var/log/syslog  /var/log/messages)</li>
              <li>:</li>
              <pre class="command-example">wget -N --no-check-certificate https://raw.githubusercontent.com/Fiftonb/GiPtato/refs/heads/main/iPtato.sh && chmod +x iPtato.sh && bash iPtato.sh</pre>
              <li></li>
            </ol>
          </el-collapse-item>
        </el-collapse>
      </div>
    </el-card>

    <el-card v-if="!isInitialized" class="initialization-card">
      <div slot="header">
        <span></span>
      </div>
      <el-steps :active="initStepActive" finish-status="success">
        <el-step v-for="(step, index) in initializationSteps" :key="index" :title="step.name"></el-step>
      </el-steps>
      <div class="initialization-actions" style="margin-top: 20px;">
        <el-button v-if="initStepActive === 1" type="primary" @click="connectServer" :loading="connecting"></el-button>
        <el-button v-if="initStepActive === 2" type="primary" @click="checkInitialization" :loading="deploying"></el-button>
        <el-button v-if="initStepActive === 2" type="warning" @click="deployIptatoManually" :loading="deploying"></el-button>
        <el-button v-if="initStepActive === 3" type="primary" @click="completeInitialization"></el-button>
        <el-button type="danger" @click="manualInitialize" :loading="loading"></el-button>
      </div>
      <div v-if="commandOutput && commandOutput.includes('')" class="error-info" style="margin-top: 15px; color: #F56C6C;">
        <p></p>
        <ul>
          <li></li>
          <li></li>
          <li></li>
        </ul>
        <p></p>
        <ul>
          <li>""</li>
          <li></li>
          <li></li>
        </ul>
      </div>
    </el-card>

    <el-card style="margin-top: 20px;">
      <div slot="header">
        <span></span>
        <el-button style="float: right; padding: 3px 0" type="text" @click="debugInfo = ''"></el-button>
      </div>
      <el-button type="warning" @click="checkScriptExistence" :loading="debugging"></el-button>
      <el-button type="warning" @click="testServerConnection" :loading="debugging"></el-button>
      <el-button type="danger" @click="resetConnectionState" :loading="debugging"></el-button>
      <el-button type="primary" @click="generateManualCommands" :loading="debugging"></el-button>
      
      <div v-if="debugInfo" class="debug-info" style="margin-top: 15px;">
        <h4></h4>
        <pre>{{ debugInfo }}</pre>
      </div>
    </el-card>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';

export default {
  name: 'RulesView',
  props: {
    serverId: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      activeTab: 'outbound',
      loading: false,
      deploying: false,
      connecting: false,
      loadingPorts: false,
      loadingIPs: false,
      server: null,
      blockList: '',
      sshPortStatus: '',
      sshPort: null,
      inboundPorts: [],
      inboundIPs: [],
      commandOutput: '',
      customPorts: '',
      customKeyword: '',
      customUnblockPorts: '',
      customUnblockKeyword: '',
      portToAllow: '',
      ipToAllow: '',
      isInitialized: false,
      initStepActive: 0,
      initializationSteps: [
        { name: '', done: false },
        { name: '', done: false },
        { name: '', done: false },
        { name: '', done: false }
      ],
      debugging: false,
      debugInfo: '',
      statusCheckTimer: null
    };
  },
  computed: {
    ...mapGetters('servers', ['getLoading']),
    hasValidServerId() {
      return !!this.serverId && this.serverId !== 'undefined';
    },
    isServerOnline() {
      return this.server && this.server.status === 'online';
    }
  },
  beforeRouteEnter(to, from, next) {
    // ID
    if (!to.params.serverId) {
      // ID
      next(vm => {
        vm.$message.error('ID');
        vm.$router.push('/servers');
      });
    } else {
      next();
    }
  },
  async created() {
    // 
    this.activeTab = 'outbound';
    
    if (this.hasValidServerId) {
      await this.checkInitialization();
      
      // 
      this.startServerStatusCheck();
    } else {
      this.commandOutput = 'ID';
      this.$message.error('ID');
    }
  },
  beforeDestroy() {
    // 
    this.stopServerStatusCheck();
  },
  methods: {
    ...mapActions('servers', [
      'getServer',
      'deployIptato',
      'connectServer'
    ]),
    ...mapActions('rules', [
      'getBlockList',
      'blockBTPTAction',
      'blockSPAMAction',
      'blockAllAction',
      'blockCustomPortsAction',
      'blockCustomKeywordAction',
      'unblockBTPTAction',
      'unblockSPAMAction',
      'unblockAllAction',
      'unblockCustomPortsAction',
      'unblockCustomKeywordAction',
      'unblockAllKeywordsAction',
      'getInboundPorts',
      'getInboundIPs',
      'allowInboundPortsAction',
      'disallowInboundPortsAction',
      'allowInboundIPsAction',
      'disallowInboundIPsAction',
      'getSSHPort',
      'clearAllRulesAction'
    ]),
    async checkInitialization() {
      try {
        if (!this.hasValidServerId) {
          this.commandOutput = 'ID';
          this.$message.error('ID');
          return false;
        }

        // 
        this.resetInitSteps();
        this.isInitialized = false;
        this.initStepActive = 0;
        
        // 1: 
        this.loading = true;
        const serverResponse = await this.getServer(this.serverId);
        if (!serverResponse || !serverResponse.success) {
          throw new Error(serverResponse?.error || '');
        }
        this.server = serverResponse.data;
        this.initializationSteps[0].done = true;
        this.initStepActive = 1;
        
        // 2: 
        if (!this.server.status || this.server.status !== 'connected') {
          this.commandOutput = '...';
          this.connecting = true;
          const connectResponse = await this.connectServer(this.serverId);
          this.connecting = false;
          
          if (!connectResponse || !connectResponse.success) {
            throw new Error(connectResponse?.error || '');
          }
          this.commandOutput += '\n';
        }
        this.initializationSteps[1].done = true;
        this.initStepActive = 2;
        
        // 3: 
        this.commandOutput += '\niptato...';
        this.deploying = true;
        try {
          const deployResponse = await this.deployIptato(this.serverId);
          this.deploying = false;
          
          if (!deployResponse || !deployResponse.success) {
            const errorMsg = deployResponse?.error || '';
            this.commandOutput += `\n: ${errorMsg}`;
            
            // 
            if (errorMsg.includes('500') || errorMsg.includes('')) {
              this.commandOutput += '\n';
              this.commandOutput += '\n1. ';
              this.commandOutput += '\n2. ';
              this.commandOutput += '\n3. ';
              this.commandOutput += '\n\n';
              this.commandOutput += '\n- ';
              this.commandOutput += '\n- ';
              this.commandOutput += '\n- ';
            }
            
            this.$message.error(`: ${errorMsg}`);
            throw new Error(errorMsg);
          }
          
          this.commandOutput += '\n';
          this.initializationSteps[2].done = true;
          this.initStepActive = 3;
        } catch (deployError) {
          this.deploying = false;
          this.commandOutput += `\n: ${deployError.message}`;
          throw deployError;
        }
        
        // 4: 
        await this.refreshBlockList();
        await this.refreshSSHPort();
        await this.refreshInboundPorts();
        await this.refreshInboundIPs();
        
        this.initializationSteps[3].done = true;
        this.isInitialized = true;
        this.loading = false;
        return true;
      } catch (error) {
        this.loading = false;
        this.deploying = false;
        this.connecting = false;
        this.commandOutput += `\n: ${error.message}`;
        this.$message.error(`: ${error.message}`);
        return false;
      }
    },
    resetInitSteps() {
      this.initializationSteps.forEach(step => step.done = false);
    },
    async refreshBlockList() {
      if (!this.hasValidServerId) {
        this.$message.error('ID');
        return;
      }
      
      try {
        this.loading = true;
        const response = await this.getBlockList(this.serverId);
        
        if (response && response.success) {
          this.blockList = response.data || '';
        } else {
          this.$message.warning(response?.error || '');
          this.blockList = '';
        }
      } catch (error) {
        this.$message.error(`: ${error.message}`);
        this.blockList = `: ${error.message}`;
      } finally {
        this.loading = false;
      }
    },
    async refreshSSHPort() {
      if (!this.hasValidServerId) {
        this.$message.error('IDSSH');
        return;
      }
      
      try {
        this.loading = true;
        const response = await this.getSSHPort(this.serverId);
        
        if (response && response.success) {
          this.sshPortStatus = response.data || 'SSH';
          
          // SSH
          try {
            const sshData = response.data;
            if (sshData && typeof sshData === 'string') {
              // 
              const portMatch = sshData.match(/SSH\s*[:]\s*(\d+)/i) || 
                              sshData.match(/\s*[:]\s*(\d+)/i) || 
                              sshData.match(/port\s*[:]\s*(\d+)/i);
              if (portMatch && portMatch[1]) {
                this.sshPort = parseInt(portMatch[1], 10);
                console.log(`SSH: ${this.sshPort}`);
              }
            }
          } catch (parseError) {
            console.error('SSH:', parseError);
            // 
            if (this.server && this.server.port) {
              this.sshPort = this.server.port;
              console.log(`: ${this.sshPort}`);
            }
          }
        } else {
          this.$message.warning(response?.error || 'SSH');
          this.sshPortStatus = 'SSH';
        }
      } catch (error) {
        this.$message.error(`SSH: ${error.message}`);
        this.sshPortStatus = `: ${error.message}`;
      } finally {
        this.loading = false;
      }
    },
    async refreshInboundPorts() {
      if (!this.hasValidServerId) {
        this.$message.error('ID');
        return;
      }
      
      try {
        this.loadingPorts = true;
        const response = await this.getInboundPorts(this.serverId);
        
        if (response && response.success) {
          const data = response.data || { tcp: [], udp: [] };
          
          // TCPUDP"TCP|UDP"
          const uniquePorts = [...new Set([...(data.tcp || []), ...(data.udp || [])])];
          const formattedPorts = uniquePorts.map(port => ({ 
            port, 
            protocol: 'TCP|UDP' 
          }));
          
          console.log(':', formattedPorts);
          this.inboundPorts = formattedPorts;
          
          if (formattedPorts.length === 0) {
            this.commandOutput = '';
          } else {
            this.commandOutput = ` ${formattedPorts.length} `;
          }
        } else {
          this.$message.warning(response?.error || '');
          this.inboundPorts = [];
          this.commandOutput = `: ${response?.error || ''}`;
        }
      } catch (error) {
        this.$message.error(`: ${error.message}`);
        this.inboundPorts = [];
        this.commandOutput = `: ${error.message}`;
      } finally {
        this.loadingPorts = false;
      }
    },
    async refreshInboundIPs() {
      if (!this.hasValidServerId) {
        this.$message.error('IDIP');
        return;
      }
      
      try {
        this.loadingIPs = true;
        const response = await this.getInboundIPs(this.serverId);
        
        if (!response || !response.success) {
          // 
          if (response && response.error && response.error.includes('')) {
            this.$message.error('');
            // 
            this.resetInitSteps();
            this.isInitialized = false;
          } else if (response && response.error && response.error.includes('')) {
            this.$message.error('');
            this.resetInitSteps();
            this.isInitialized = false;
          } else {
            this.$message.warning(response?.error || 'IP');
          }
          this.inboundIPs = [];
          this.commandOutput = `IP: ${response?.error || ''}`;
          return;
        }
        
        // IP
        const ipData = response.data || [];
        
        // 
        if (Array.isArray(ipData)) {
          // IP
          this.inboundIPs = ipData.map(ip => ({ ip }));
          
          if (ipData.length === 0) {
            this.commandOutput = 'IP';
          } else {
            this.commandOutput = `IP ${ipData.length} IP`;
          }
        } else {
          this.$message.warning('IP');
          this.inboundIPs = [];
          this.commandOutput = 'IP';
        }
      } catch (error) {
        this.$message.error(`IP: ${error.message}`);
        this.inboundIPs = [];
        this.commandOutput = `IP: ${error.message}`;
      } finally {
        this.loadingIPs = false;
      }
    },
    async executeTestCommand() {
      if (!this.hasValidServerId) {
        this.commandOutput = 'ID';
        this.$message.error('ID');
        return;
      }
      
      // ...
    },
    async blockBTPT() {
      if (!this.hasValidServerId) {
        this.$message.error('ID');
        return;
      }
      
      try {
        this.loading = true;
        const response = await this.blockBTPTAction(this.serverId);
        
        if (response && response.success) {
          this.$message.success('BT/PT');
          await this.refreshBlockList();
        } else {
          this.$message.error(response?.error || 'BT/PT');
        }
      } catch (error) {
        this.$message.error(`BT/PT: ${error.message}`);
      } finally {
        this.loading = false;
      }
    },
    async blockSPAM() {
      if (!this.hasValidServerId) {
        this.$message.error('ID');
        return;
      }
      
      try {
        this.loading = true;
        const response = await this.blockSPAMAction(this.serverId);
        
        if (response && response.success) {
          this.$message.success('');
          await this.refreshBlockList();
        } else {
          this.$message.error(response?.error || '');
        }
      } catch (error) {
        this.$message.error(`: ${error.message}`);
      } finally {
        this.loading = false;
      }
    },
    async blockAll() {
      if (!this.hasValidServerId) {
        this.$message.error('ID');
        return;
      }
      
      try {
        this.loading = true;
        const response = await this.blockAllAction(this.serverId);
        
        if (response && response.success) {
          this.$message.success('');
          await this.refreshBlockList();
        } else {
          this.$message.error(response?.error || '');
        }
      } catch (error) {
        this.$message.error(`: ${error.message}`);
      } finally {
        this.loading = false;
      }
    },
    async blockCustomPorts() {
      if (!this.hasValidServerId) {
        this.$message.error('ID');
        return;
      }
      
      if (!this.customPorts) {
        this.$message.warning('');
        return;
      }
      
      try {
        this.loading = true;
        const response = await this.blockCustomPortsAction({
          serverId: this.serverId,
          ports: this.customPorts
        });
        
        if (response && response.success) {
          this.$message.success(`: ${this.customPorts}`);
          this.customPorts = '';
          await this.refreshBlockList();
        } else {
          this.$message.error(response?.error || '');
        }
      } catch (error) {
        this.$message.error(`: ${error.message}`);
      } finally {
        this.loading = false;
      }
    },
    async blockCustomKeyword() {
      if (!this.hasValidServerId) {
        this.$message.error('ID');
