      }
      
      try {
        this.loading = true;
        const response = await this.unblockAllKeywordsAction(this.serverId);
        
        if (response && response.success) {
          this.$message.success('');
          await this.refreshBlockList();
        } else {
          this.$message.error(response?.error || '');
        }
      } catch (error) {
        this.$message.error(`: ${error.message}`);
      } finally {
        this.loading = false;
      }
    },
    async allowPort() {
      if (!this.hasValidServerId) {
        this.$message.error('ID');
        return;
      }
      
      if (!this.portToAllow) {
        this.$message.warning('');
        return;
      }
      
      try {
        this.loading = true;
        const response = await this.allowInboundPortsAction({
          serverId: this.serverId,
          ports: this.portToAllow
        });
        
        if (response && response.success) {
          this.$message.success(`: ${this.portToAllow}`);
          this.portToAllow = '';
          await this.refreshInboundPorts();
        } else {
          this.$message.error(response?.error || '');
        }
      } catch (error) {
        this.$message.error(`: ${error.message}`);
      } finally {
        this.loading = false;
      }
    },
    async disallowPort(port) {
      if (!this.hasValidServerId) {
        this.$message.error('ID');
        return;
      }
      
      // SSH
      if (this.isSshPort(port)) {
        this.$message.error('SSH');
        return;
      }
      
      try {
        this.loading = true;
        const response = await this.disallowInboundPortsAction({
          serverId: this.serverId,
          ports: port.toString()
        });
        
        if (response && response.success) {
          this.$message.success(`: ${port}`);
          await this.refreshInboundPorts();
        } else {
          this.$message.error(response?.error || '');
        }
      } catch (error) {
        this.$message.error(`: ${error.message}`);
      } finally {
        this.loading = false;
      }
    },
    async allowIP() {
      if (!this.hasValidServerId) {
        this.$message.error('ID');
        return;
      }
      
      if (!this.ipToAllow) {
        this.$message.warning('IP');
        return;
      }
      
      try {
        this.loading = true;
        const response = await this.allowInboundIPsAction({
          serverId: this.serverId,
          ips: this.ipToAllow
        });
        
        if (response && response.success) {
          this.$message.success(`IP: ${this.ipToAllow}`);
          this.ipToAllow = '';
          await this.refreshInboundIPs();
        } else {
          this.$message.error(response?.error || 'IP');
        }
      } catch (error) {
        this.$message.error(`IP: ${error.message}`);
      } finally {
        this.loading = false;
      }
    },
    async disallowIP(ip) {
      if (!this.hasValidServerId) {
        this.$message.error('ID');
        return;
      }
      
      // IP
      const ipAddress = typeof ip === 'object' ? ip.ip : ip;
      
      if (!ipAddress) {
        this.$message.error('IP');
        return;
      }
      
      try {
        this.loading = true;
        const response = await this.disallowInboundIPsAction({
          serverId: this.serverId,
          ips: ipAddress
        });
        
        if (response && response.success) {
          this.$message.success(`IP: ${ipAddress}`);
          await this.refreshInboundIPs();
        } else {
          this.$message.error(response?.error || 'IP');
        }
      } catch (error) {
        this.$message.error(`IP: ${error.message}`);
      } finally {
        this.loading = false;
      }
    },
    confirmClearRules() {
      if (!this.hasValidServerId) {
        this.$message.error('ID');
        return;
      }
      
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.clearAllRules();
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });          
      });
    },
    async clearAllRules() {
      if (!this.hasValidServerId) {
        this.$message.error('ID');
        return;
      }
      
      try {
        this.loading = true;
        const response = await this.clearAllRulesAction(this.serverId);
        
        if (response && response.success) {
          this.$message.success('');
          await this.refreshBlockList();
          await this.refreshInboundPorts();
          await this.refreshInboundIPs();
        } else {
          this.$message.error(response?.error || '');
        }
      } catch (error) {
        this.$message.error(`: ${error.message}`);
      } finally {
        this.loading = false;
      }
    },
    async deployIptatoManually() {
      if (!this.hasValidServerId) {
        this.$message.error('ID');
        return;
      }
      
      try {
        this.deploying = true;
        this.commandOutput = '...\n';
        
        // 
        const response = await this.$store.dispatch('servers/executeCommand', {
          serverId: this.serverId,
          command: 'wget -N --no-check-certificate https://raw.githubusercontent.com/Fiftonb/GiPtato/refs/heads/main/iPtato.sh && chmod +x iPtato.sh && bash iPtato.sh'
        });
        
        if (response && response.success) {
          this.commandOutput += '...\n';
          
          // 
          const verifyResponse = await this.$store.dispatch('servers/executeCommand', {
            serverId: this.serverId,
            command: 'test -f /root/iptato.sh && echo "installed" || echo "not found"'
          });
          
          if (verifyResponse && verifyResponse.success && 
              verifyResponse.data && verifyResponse.data.stdout && 
              verifyResponse.data.stdout.includes('installed')) {
            
            this.commandOutput += '!\n';
            this.$message.success('');
            this.initializationSteps[2].done = true;
            this.initStepActive = 3;
            
            // 
            await this.refreshBlockList();
            await this.refreshSSHPort();
            await this.refreshInboundPorts();
            await this.refreshInboundIPs();
            
            this.initializationSteps[3].done = true;
            this.isInitialized = true;
          } else {
            this.commandOutput += '\n';
            this.$message.error('');
          }
        } else {
          this.commandOutput += `: ${response?.error || ''}\n`;
          this.$message.error('');
        }
      } catch (error) {
        this.commandOutput += `: ${error.message}\n`;
        this.$message.error(`: ${error.message}`);
      } finally {
        this.deploying = false;
      }
    },
    async completeInitialization() {
      if (!this.hasValidServerId) {
        this.$message.error('ID');
        return;
      }
      
      try {
        this.loading = true;
        this.commandOutput = '...\n';
        
        // 
        await this.refreshBlockList();
        await this.refreshSSHPort();
        await this.refreshInboundPorts();
        await this.refreshInboundIPs();
        
        // 
        this.initializationSteps[3].done = true;
        this.isInitialized = true;
        this.$message.success('');
        this.commandOutput += '';
      } catch (error) {
        this.commandOutput += `\n: ${error.message}`;
        this.$message.error(`: ${error.message}`);
      } finally {
        this.loading = false;
      }
    },
    clearCommandOutput() {
      this.commandOutput = '';
    },
    async checkScriptExistence() {
      if (!this.hasValidServerId) {
        this.$message.error('ID');
        return;
      }
      
      try {
        this.debugging = true;
        this.debugInfo = '...\n';
        
        // 
        const commands = [
          'ls -la /root/iPtato.sh',
          'ls -la /root/iptato.sh',
          'find /root -name "*.sh" | grep -i iptato',
          'find / -name "*.sh" -type f -not -path "*/\\.*" | grep -i iptato 2>/dev/null'
        ];
        
        for (const command of commands) {
          this.debugInfo += `\n: ${command}\n`;
          const response = await this.$store.dispatch('servers/executeCommand', {
            serverId: this.serverId,
            command
          });
          
          if (response && response.success) {
            const stdout = response.data?.stdout || '';
            const stderr = response.data?.stderr || '';
            
            this.debugInfo += `:\n${stdout}\n`;
            if (stderr) {
              this.debugInfo += `:\n${stderr}\n`;
            }
            
            if (stdout && (stdout.includes('iPtato.sh') || stdout.includes('iptato.sh'))) {
              this.debugInfo += '\n\n';
              this.debugInfo += '\n';
              this.$message.warning('');
              break;
            }
          } else {
            this.debugInfo += `: ${response?.error || ''}\n`;
          }
        }
        
        // 
        this.debugInfo += '\n...\n';
        const execResponse = await this.$store.dispatch('servers/executeCommand', {
          serverId: this.serverId,
          command: 'cd /root && (./iPtato.sh --help || ./iptato.sh --help || echo "")'
        });
        
        if (execResponse && execResponse.success) {
          const stdout = execResponse.data?.stdout || '';
          this.debugInfo += `:\n${stdout}\n`;
          
          if (stdout.includes('') || stdout.includes('iptables')) {
            this.debugInfo += '\n\n';
            this.debugInfo += '\n';
            this.$message.success('');
          }
        } else {
          this.debugInfo += `: ${execResponse?.error || ''}\n`;
        }
      } catch (error) {
        this.debugInfo += `\n: ${error.message}\n`;
        this.$message.error(`: ${error.message}`);
      } finally {
        this.debugging = false;
      }
    },
    async testServerConnection() {
      if (!this.hasValidServerId) {
        this.$message.error('ID');
        return;
      }
      
      try {
        this.debugging = true;
        this.debugInfo = '...\n';
        
        // 
        this.debugInfo += '1. :\n';
        const serverResponse = await this.getServer(this.serverId);
        if (serverResponse && serverResponse.success) {
          this.debugInfo += `: ${JSON.stringify(serverResponse.data, null, 2)}\n`;
          this.debugInfo += `: ${serverResponse.data.status}\n`;
        } else {
          this.debugInfo += `: ${serverResponse?.error || ''}\n`;
        }
        
        // 
        this.debugInfo += '\n...\n';
        try {
          const connectResponse = await this.connectServer(this.serverId);
          if (connectResponse && connectResponse.success) {
            this.debugInfo += '\n';
          } else {
            this.debugInfo += `: ${connectResponse?.error || ''}\n`;
          }
        } catch (connError) {
          this.debugInfo += `: ${connError.message}\n`;
        }
        
        // 
        this.debugInfo += '\n2. :\n';
        const commandResponse = await this.$store.dispatch('servers/executeCommand', {
          serverId: this.serverId,
          command: 'uname -a && whoami && pwd'
        });
        
        if (commandResponse && commandResponse.success) {
          this.debugInfo += `:\n${commandResponse.data?.stdout || ''}\n`;
          this.debugInfo += `\n`;
        } else {
          this.debugInfo += `: ${commandResponse?.error || ''}\n`;
          this.debugInfo += `\n`;
        }
        
        // API
        this.debugInfo += '\n3. :\n';
        const baseURL = process.env.VUE_APP_API_URL || window.location.origin;
        this.debugInfo += `APIURL: ${baseURL}\n`;
        this.debugInfo += `: ${process.env.NODE_ENV}\n`;
        
        // 
        this.debugInfo += '\n4. :\n';
        try {
          const pingResponse = await this.$store.dispatch('servers/executeCommand', {
            serverId: this.serverId,
            command: 'ping -c 3 8.8.8.8'
          });
          
          if (pingResponse && pingResponse.success) {
            this.debugInfo += `ping:\n${pingResponse.data?.stdout || ''}\n`;
          } else {
            this.debugInfo += `ping: ${pingResponse?.error || ''}\n`;
          }
        } catch (error) {
          this.debugInfo += `ping: ${error.message}\n`;
        }
        
        this.$message.info('');
      } catch (error) {
        this.debugInfo += `\n: ${error.message}\n`;
        this.$message.error(`: ${error.message}`);
      } finally {
        this.debugging = false;
      }
    },
    async resetConnectionState() {
      if (!this.hasValidServerId) {
        this.$message.error('ID');
        return;
      }
      
      try {
        this.debugging = true;
        this.debugInfo = '...\n';
        
        // 
        try {
          this.debugInfo += '...\n';
          const disconnectCommand = await this.$store.dispatch('servers/executeCommand', {
            serverId: this.serverId,
            command: 'echo ""'
          });
          
          this.debugInfo += ': ' + 
            (disconnectCommand?.success ? '' : '') + '\n';
        } catch (disconnectError) {
          this.debugInfo += `: ${disconnectError.message}\n`;
        }
        
        // 
        this.debugInfo += '...\n';
        
        try {
          const connectResponse = await this.connectServer(this.serverId);
          if (connectResponse && connectResponse.success) {
            this.debugInfo += '\n';
          } else {
            this.debugInfo += `: ${connectResponse?.error || ''}\n`;
          }
        } catch (connError) {
          this.debugInfo += `: ${connError.message}\n`;
        }
        
        // 
        this.resetInitSteps();
        this.isInitialized = false;
        this.initStepActive = 0;
        
        // 
        await this.checkInitialization();
        this.debugInfo += '\n';
        this.$message.success('');
      } catch (error) {
        this.debugInfo += `\n: ${error.message}\n`;
        this.$message.error(`: ${error.message}`);
      } finally {
        this.debugging = false;
      }
    },
    async manualInitialize() {
      if (!this.hasValidServerId) {
        this.$message.error('ID');
        return;
      }
      
      try {
        this.loading = true;
        this.commandOutput = '...\n';
        
        // 
        this.initializationSteps.forEach(step => step.done = true);
        this.isInitialized = true;
        
        // 
        await this.refreshBlockList();
        await this.refreshSSHPort();
        await this.refreshInboundPorts();
        await this.refreshInboundIPs();
        
        this.commandOutput += '\n';
        this.$message.success('');
      } catch (error) {
        this.commandOutput += `\n: ${error.message}\n`;
        this.$message.error(`: ${error.message}`);
      } finally {
        this.loading = false;
      }
    },
    
    // 
    async generateManualCommands() {
      if (!this.hasValidServerId) {
        this.$message.error('ID');
        return;
      }
      
      try {
        this.debugging = true;
        this.debugInfo = '\n\n';
        
        // 1. iPtato
        this.debugInfo += '## 1. iPtato\n';
        this.debugInfo += '```\n';
        this.debugInfo += 'cd ~ && wget -N --no-check-certificate https://raw.githubusercontent.com/Fiftonb/GiPtato/refs/heads/main/iPtato.sh && chmod +x iPtato.sh\n';
        this.debugInfo += '```\n\n';
        
        // 2. iPtato
        this.debugInfo += '## 2. iPtato\n';
        this.debugInfo += '```\n';
        this.debugInfo += './iPtato.sh\n';
        this.debugInfo += '```\n\n';
        
        // 3. 
        this.debugInfo += '## 3. \n';
        this.debugInfo += '```\n';
        this.debugInfo += '# BT/PT\n';
        this.debugInfo += './iPtato.sh 1\n\n';
        this.debugInfo += '# BT/PT\n';
        this.debugInfo += './iPtato.sh 11\n\n';
        this.debugInfo += '# \n';
        this.debugInfo += './iPtato.sh 101\n';
        this.debugInfo += '```\n\n';
        
        // 
        this.debugInfo += '## \n';
        this.debugInfo += '1. SSH\n';
        this.debugInfo += '2. SSH\n';
        this.debugInfo += '3. ""\n\n';
        
        this.debugInfo += '""\n';
        
        this.$message.success('');
      } catch (error) {
        this.debugInfo += `\n: ${error.message}\n`;
        this.$message.error(`: ${error.message}`);
      } finally {
        this.debugging = false;
      }
    },
    async deployIptatoScript() {
      if (!this.hasValidServerId) {
        this.$message.error('ID');
        return;
      }
      
      try {
        this.deploying = true;
        this.commandOutput = '...\n';
        
        const response = await this.deployIptato(this.serverId);
        
        if (response && response.success) {
          this.$message.success('');
          this.commandOutput += '\n';
        } else {
          const errorMsg = response?.error || '';
          this.$message.error(`: ${errorMsg}`);
          this.commandOutput += `\n: ${errorMsg}`;
        }
      } catch (error) {
        this.$message.error(`: ${error.message}`);
        this.commandOutput += `\n: ${error.message}`;
      } finally {
        this.deploying = false;
      }
    },
    isSshPort(port) {
      // APISSH
      if (this.sshPort && this.sshPort === parseInt(port, 10)) {
        return true;
      }
      
      // 
      if (this.server && this.server.port === parseInt(port, 10)) {
        return true;
      }
      
      // SSH
      const commonSshPorts = [22, 2222];
      return commonSshPorts.includes(parseInt(port, 10));
    },
    // 
    startServerStatusCheck() {
      // 30
      this.statusCheckTimer = setInterval(async () => {
        if (this.hasValidServerId) {
          try {
            const response = await this.getServer(this.serverId);
            if (response && response.success) {
              const newStatus = response.data.status;
              const oldStatus = this.server ? this.server.status : null;
              
              // 
              this.server = response.data;
              
              // 
              if (oldStatus !== 'online' && newStatus === 'online') {
                this.$message.success('');
              }
              
              // 
              if (oldStatus === 'online' && newStatus !== 'online') {
                this.$message.warning('');
              }
            }
          } catch (error) {
            console.error(':', error);
          }
        }
      }, 30000); // 30
    },
    
    // 
    stopServerStatusCheck() {
      if (this.statusCheckTimer) {
        clearInterval(this.statusCheckTimer);
        this.statusCheckTimer = null;
      }
    }
  }
};
</script>

